import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import catImage from "../cat.jpg";
import sleepingCat from "../Sleeping_cat.jpg";
import green from "../EAT_green.jpg";

function ImageStory() {
  const [image, setImage] = useState(null);
  const navigate = useNavigate();
  const handleFileChange = (e) => {
    setImage(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (image) {
      //        URL 
      const reader = new FileReader();
      reader.onload = () => {
        navigate("/image-preview", { state: { image: reader.result } });
      };
      reader.readAsDataURL(image);
    }
  };

  return (
    <div className="ImageStory">
      <div className="site-title">SogeumStory</div>
      <div className="image-title">
         <br></br>
          !
      </div>
      <span className="exmaple-text">  ?</span>
      <div className="image-example">
        <img src={catImage} alt="" />
        <img src={sleepingCat} alt="" />
        <img src={green} alt="" />
      </div>
      <form onSubmit={handleSubmit}>
        <div className="file-upload-wrapper">
          <input
            className="image-input"
            type="file"
            id="file-upload"
            accept="image/*"
            onChange={handleFileChange}
          />
          <label htmlFor="file-upload" className="custom-file-upload">
             
          </label>
          {image ? (
            <button className="input-btn" type="submit" disabled={!image}>
              
            </button>
          ) : (
            <></>
          )}
        </div>
      </form>
    </div>
  );
}

export default ImageStory;
