import React, { useState, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import M from "materialize-css";

function TextDetails() {
  const [mainCharacter, setMainCharacter] = useState("");
  const [genre, setGenre] = useState("");
  const location = useLocation();
  const navigate = useNavigate();
  const { keyword } = location.state;

  useEffect(() => {
    // Materialize select  
    M.FormSelect.init(document.querySelectorAll("select"));
  }, []);

  const handleGenerate = (e) => {
    e.preventDefault();

    if (!mainCharacter || !genre) {
      console.error("Main Character and Genre are required");
      return;
    }

    navigate("/text-result", { state: { keyword, mainCharacter, genre } });
  };

  return (
    <div className="DetailsPage">
      <div className="site-title">SogeumStory</div>
      <div className="detail-title">
        
        <br></br>  !
      </div>
      <span className="detail-text">  ?</span>
      <div className="input-field col s6">
        <input
          id="mainCharacter"
          type="text"
          className="validate"
          value={mainCharacter}
          placeholder=" ."
          onChange={(e) => setMainCharacter(e.target.value)}
        />
      </div>

      <span className="detail-text"> !</span>
      <div className="input-field col s12">
        <select value={genre} onChange={(e) => setGenre(e.target.value)}>
          <option value="" disabled>
            
          </option>
          <option value="">
            <span className="blue-text"></span>
          </option>
          <option value="SF">
            <span className="blue-text">SF</span>
          </option>
          <option value="">
            <span className="blue-text"></span>
          </option>
          <option value="">
            <span className="blue-text"></span>
          </option>
          <option value="">
            <span className="blue-text"></span>
          </option>
        </select>
        {/* <label> </label> */}
      </div>

      <div className="generate-btn">
        <button
          className="btn waves-effect waves-light"
          type="submit"
          name="action"
          onClick={handleGenerate}
          disabled={!mainCharacter || !genre}
        >
            <i className="material-icons right">send</i>
        </button>
      </div>
    </div>
  );
}

export default TextDetails;
